
.Icon {
  cursor:pointer;
  display:flex;
  justify-content:center;
  align-items:center;
  height:48px;
  width:48px;
  font-size:24px;
  font-weight:700;
}
.Icon-check {
  position:absolute;
  left:12px;
}
.Icon-check--active {
  color:#4caf50;
}
.Icon-delete {
  position:absolute;
  top:-24px;
  right:0;
}
.Icon-delete:hover {
  color:red;
}
.Icon-svg {
  width: 24px;
}